gsap.registerPlugin(ScrollTrigger);

let scaleImages = document.querySelectorAll(".js-scroll-scale-img img")
scaleImages.forEach(img => {
    gsap.to(img, {
        scale: 1,
        scrollTrigger: {
            trigger: img,
            start: 'top 30% top',
            // end: 'bottom -5%',
            scrub: true,
        }
    })
})
